@import url(https://fonts.googleapis.com/css2?family=Courgette&family=Montserrat:wght@300&family=Neucha&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Courgette', cursive;
}

.container {
  width: 100vw;
  min-height: 100vh;
  background-color: lightgreen;
}

.container .nav {
  top: 2vh;
  width: 100vw;
  height: 10vh;
  position: fixed;
  background-color: rgba(53, 112, 56, 0.61);
  text-align: center;
  color: lightyellow;
  letter-spacing: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container .main_body {
  transform: translateY(15vh);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container .main_body input {
  font-family: 'Pacifico', cursive;
  font-size: 1.5rem;
  width: 70vw;
  height: 4vh;
  outline: none;
  border: 0;
  margin-bottom: 2vh;
  margin-right: 2vw;
  transition: border .2s linear;
  border-radius: 1rem;
}

.container .main_body input:focus {
  outline: none;
  border-bottom: 2px solid black;
}

.container .main_body button {
  font-family: 'Pacifico', cursive;
  font-size: 1.4rem;
  min-height: 4vh;
  min-width: 8vw;
  border-radius: 1rem;
  background-color: lightslategray;
  color: white;
  outline: none;
  border: 0;
}

.container .main_body button:hover {
  background-color: lawngreen;
  color: white;
}

.container .main_body ul {
  list-style: none;
}

.container .main_body ul li {
  font-family: 'Neucha', cursive;
  font-size: 1.5rem;
  background-color: white;
  width: 70vw;
  min-height: 5vh;
  border-bottom: 1px solid rgba(119, 136, 153, 0.13);
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.container .main_body ul li:first-child {
  border-top-right-radius: 10rem;
}

.container .main_body ul li:last-child {
  border-bottom-left-radius: 10rem;
}

.container .main_body h2 {
  margin-bottom: 2vh;
  font-size: 1.5rem;
}
